<template>
	<vx-card>
		<vs-table v-model="selected" @selected="handleSelected" :data="datos" max-items="8" pagination search noDataText="No hay campañas en tu búsqueda" hoverFlat>
			<template slot="thead">
				<vs-th sort-key="id">ID</vs-th>
				<vs-th sort-key="campana">Campaña</vs-th>
				<vs-th sort-key="fechaIni" style="width:114px">Fecha Inicio</vs-th>
				<vs-th sort-key="fechaFin" style="width:98px; padding-right:0px;">Fecha Fin</vs-th>
				<vs-th>Intensidad Diaria por Medios Usados</vs-th>
				<vs-th sort-key="duracion" style="padding-left:0px; padding-right:0px; width: 50px">Duración</vs-th>
				<vs-th>Estado</vs-th>
				<vs-th></vs-th>
			</template>

			<template  v-if="datos.length != 0" slot-scope="{data}">
				<vs-tr :data="tr" :key="i" v-for="(tr, i) in data">

					<vs-td :data="data[i].id">
						{{data[i].id}}
					</vs-td>

					<vs-td :data="data[i].nombre">
						{{data[i].nombre}}
					</vs-td>

					<vs-td :data="data[i].fechaInicio">
						{{data[i].fechaInicio}}
					</vs-td>

					<vs-td :data="data[i].fechaFin" style="padding-right:0px;">
						{{data[i].fechaFin}}
					</vs-td>

					<vs-td :data="data[i].medios" style="padding-right:0px; padding-left:0px">
						<center>
							<span v-for="item in data[i].medios" :key="item.id" class="inline-flex relative">
								<h3 style="padding-right:5px; padding-left:14px">{{ item.intensidad }}</h3>
								<IconifyIcon v-if="item.id =='1'" :icon="iconos.CelularIcon" width="25" />
								<IconifyIcon v-else-if="item.id =='2'" :icon="iconos.TelefonoIcon" width="25" />
								<IconifyIcon v-else-if="item.id =='3'" :icon="iconos.EmailIcon" width="25" />
								<IconifyIcon v-else-if="item.id =='4'" :icon="iconos.SMSIcon" width="25" />
								<IconifyIcon v-else-if="item.id =='5'" :icon="iconos.WhatsappIcon" width="25" />
							</span>
						</center>
					</vs-td>

					<vs-td :data="data[i].duracion" style="padding-left:0px; padding-right:0px; width: 50px">
						<center>
							{{data[i].duracion}} días
						</center>
					</vs-td>

					<vs-td :data="data[i].estado" style="padding-left:0px; padding-right:0px;">
						<center>
						<span class="inline-flex relative">
							<span v-if="data[i].estado == 1" class="p-1 inline-flex rounded-full feather-icon select-none relative text-primary mb-4" style="background: rgba(var(--vs-success),0.15);">
								<vs-icon icon="mood" size="25px" color="success"></vs-icon>
								<p style="color:rgba(var(--vs-success)); font-size:10px;">Activo</p>
							</span>
							<span v-else-if="data[i].estado == 2" class="p-1 inline-flex rounded-full feather-icon select-none relative text-primary mb-4" style="background: rgba(var(--vs-warning),0.15);">
								<vs-icon icon="history" size="25px" color="warning"></vs-icon>
								<p style="color:rgba(var(--vs-warning)); font-size:10px">Programado</p>
							</span>
							<span v-else-if="data[i].estado == 3" class="p-1 inline-flex rounded-full feather-icon select-none relative text-primary mb-4" style="background: rgba(var(--vs-danger),0.15);">
								<vs-icon icon="check_circle" size="25px" color="danger"></vs-icon>
								<p style="color:rgba(var(--vs-danger)); font-size:10px">Finalizado</p>
							</span>
						</span>
						</center>
					</vs-td>
					<vs-td style="padding-left:0px; padding-right:0px; width: 50px">
						<vs-dropdown>
							<vs-button radius color="#000000" type="flat" icon-pack="feather" icon="icon-more-vertical"></vs-button>
							<vs-dropdown-menu>
								<vs-dropdown-item v-if="data[i].estado == 1 || data[i].estado == 2" @click="editarCampana(i)"> Editar </vs-dropdown-item>
								<vs-dropdown-item v-if="data[i].estado == 1 || data[i].estado == 2" @click="finalizarCampana(i)" divider> Finalizar </vs-dropdown-item>
								<vs-dropdown-item v-if="data[i].estado == 3" divider> Estadísticas </vs-dropdown-item>
							</vs-dropdown-menu>
						</vs-dropdown>
					</vs-td>
				</vs-tr>
			</template>
		</vs-table>

		<!-- Modal para Editar una Campaña -->
		<vs-prompt  v-if="datos.length != 0"
			title="Editar Campaña"
			accept-text="Cambiar"
			cancel-text="Cancelar"
			:is-valid="validName"
			@cancel="cancelarCambios"
			@accept="aceptarCambios"
			@close="cancelarCambios"
			:active.sync="modalCambios">
			<div class="con-exemple-prompt">
				Nombre de la campaña:
				<vs-input placeholder="Nombre de la Campaña" v-model="datos[tmpI].nombre" class="mt-4 mb-2 w-full" />
				<div v-if="datos[tmpI].estado == 2" class="vx-row">
					<div class="vx-col w-full md:w-1/2 lg:w-1/2 xl:w-1/2">
						<center>
							<p class="pt-3 pr-3">Fecha de Inicio</p>
							<flat-pickr :config="configFechaInicio" :placeholder="datos[tmpI].fechaInicio" v-model="datos[tmpI].fechaInicio" @on-change="onFromChange" />
						</center>
					</div>
					<div class="vx-col w-full md:w-1/2 lg:w-1/2 xl:w-1/2">
						<center>
							<p class="pt-3 pr-3">Fecha Fin</p>
							<flat-pickr :config="configFechaFin" :placeholder="datos[tmpI].fechaFin" v-model="datos[tmpI].fechaFin" @on-change="onToChange" />
						</center>
					</div>
				</div>
				<div v-if="datos[tmpI].estado == 1" class="vx-row">
					<div class="vx-col w-full">
						<center>
							<p class="pt-3 pr-3">Fecha Fin</p>
							<flat-pickr :config="configFechaFin" :placeholder="datos[tmpI].fechaFin" v-model="datos[tmpI].fechaFin" @on-change="onToChange" />
						</center>
					</div>
				</div>		
			</div>
		</vs-prompt>


		<!-- Modal para Finalizar una Campaña -->
        <vs-prompt v-if="datos.length != 0"
			title="Finalizar Campaña"
			accept-text="Finalizar Campaña"
			cancel-text="Cancelar"
			@cancel="cancelarFinalizado"
			@accept="confirmarFinalizado"
			@close="cancelarFinalizado"
			:active.sync="modalFinalizado">
			<div class="con-exemple-prompt">
                <vs-row>
                    <center><p>¿Estas seguro de finalizar la campaña de {{datos[tmpI].nombre}}?</p></center>
                </vs-row>             
			</div>
		</vs-prompt>
	</vx-card>
</template>

<script>
import IconifyIcon from '@iconify/vue';
import CelularIcon from '@iconify/icons-ic/baseline-local-phone';
import TelefonoIcon from '@iconify/icons-entypo/old-phone';
import EmailIcon from '@iconify/icons-ic/round-email';
import SMSIcon from '@iconify/icons-ic/baseline-sms';
import WhatsappIcon from '@iconify/icons-dashicons/whatsapp';
import flatPickr from 'vue-flatpickr-component';
import 'flatpickr/dist/flatpickr.css';
import moment from 'moment';
import axios from 'axios';

export default {
	props: ['datos'],
	components: {
		IconifyIcon,
		flatPickr
	},
	data () {
		return {
			modalFinalizado: false,
			modalCambios: false,
			iconos: {
				CelularIcon,
				TelefonoIcon,
				EmailIcon,
				SMSIcon,
				WhatsappIcon,
			},
			selected: [],
			tmpI: 0,
			tmpC: [],
			tmpA: {
				nombre:'',
				fecha1:'',
				fecha2:'',
			},
			configFechaInicio: {
				minDate: new Date(),
				maxDate: null,
				dateFormat: 'd-m-Y',
            },
            configFechaFin: {
				minDate: new Date(),
				dateFormat: 'd-m-Y',
            },
		}
	},
	methods: {
		editarCampana(i){
			this.modalCambios = true; 
			this.tmpI = i;
			this.tmpA.nombre = this.datos[i].nombre;
			this.tmpA.fechae1 = this.datos[i].fechaInicio;
			this.tmpA.fechae2 = this.datos[i].fechaFin;
		},
		onFromChange(selectedDates, dateStr, instance) {
			this.$set(this.configFechaFin, 'minDate', dateStr);
		},
		onToChange(selectedDates, dateStr, instance) {
			this.$set(this.configFechaInicio, 'maxDate', dateStr);
		},
		handleSelected (tr) {
			this.$vs.notify({
				title: `Seleccionada Campaña ${tr.id}`,
				text: `Nombre: ${tr.nombre}`
			})
			this.$router.push({ name: 'estadistica',  params: { idCampana: tr.id } });
		},
		aceptarCambios(){
			let id = this.datos[this.tmpI].id;
			let nombre = this.datos[this.tmpI].nombre;
			let fechaInicio = this.datos[this.tmpI].fechaInicio;
			let fechaFin = this.datos[this.tmpI].fechaFin;
			let duracion = moment(this.datos[this.tmpI].fechaFin, 'DD-MM-YYYY').diff(moment(this.datos[this.tmpI].fechaInicio, 'DD-MM-YYYY'), 'days')+1;
			this.datos[this.tmpI].duracion = duracion;
			let data = {
				id: id,
				nombre: nombre,
				fechaInicio: fechaInicio,
				fechaFin: fechaFin,
				duracion: String(duracion)
			}
			axios.put(this.url.actualizarCampanias, data).then((res) =>{
				this.$vs.notify({
					color:'success',
					title:'Cambios Guardados',
				});
				this.datos[this.tmpI].duracion = duracion;
			}).catch((error) => {
				this.alerta('danger', 'Ocurrió un error a cargar la DB', 'Error: ' + error);
				console.error('Error: ',error);
			});
		},
		cancelarCambios(){
			this.datos[this.tmpI].nombre = this.tmpA.nombre;
		},
		finalizarCampana(i){
			this.modalFinalizado = true; 
			this.tmpI = i;
			this.tmpA.nombre = this.datos[i].nombre;
			this.tmpA.fechae1 = this.datos[i].fechaInicio;
			this.tmpA.fechae2 = this.datos[i].fechaFin;
		},
		cancelarFinalizado(){
			this.$vs.notify({
				color:'danger',
				title:'Finalizada Cancelada',
			});
		},
		confirmarFinalizado(){
			let id = this.datos[this.tmpI].id;
			let fechaFin = String(moment().format('DD-MM-YYYY'));
			let duracion  = moment(fechaFin, 'DD-MM-YYYY').diff(moment(this.datos[this.tmpI].fechaInicio, 'DD-MM-YYYY'), 'days')+1 ;
			this.datos[this.tmpI].duracion = duracion;
			this.datos[this.tmpI].fechaFin = fechaFin;
			let data = {
				id: id,
				fechaFin: fechaFin,
				duracion: duracion
			}
			axios.put(this.url.finalizarCampanias, data).then((res) =>{
                    this.$vs.notify({
                        color:'success',
                        title:'Campaña Finalizada Correctamente',
                        iconPack: 'feather', icon:'icon-check'
					});
					this.datos[this.tmpI].estado = "3";
					this.datos[this.tmpI].fechaFin = String(moment().format('DD-MM-YYYY'));
					this.datos[this.tmpI].duracion  = moment(this.datos[this.tmpI].fechaFin, 'DD-MM-YYYY').diff(moment(this.datos[this.tmpI].fechaInicio, 'DD-MM-YYYY'), 'days')+1 ;
                }).catch((error) => {
                    console.log(error);
                    this.$vs.notify({
                        color:'danger',
                        title:'Error al Finalizar Campaña',
                        text: `No se pudo finalizar la campaña ${this.tmpA.nombre}. Error: ${error}`,
                        iconPack: 'feather', icon:'icon-alert-circle'
                    });
			})	
		}
	},
	computed:{
		validName(){
			return (this.datos[this.tmpI].nombre.length > 0)
		},
		url(){
			return this.$store.state.urlsAPI;
		},
		infoUser(){
			return this.$store.state.loginInfo;
		}
	},
	
}
</script>



<style lang="scss">
.dropdown-button-container {
	display: flex;
	align-items: center;

	.btnx {
		border-radius: 5px 0px 0px 5px;
	}

	.btn-drop {
		border-radius: 0px 5px 5px 0px;
		border-left: 1px solid rgba(255, 255, 255, .2);
	}
}
.flatpickr-input{
    width: 95px;
}
.flatpickr-input.h{
    width: 78px;
}
</style>
