<template>
	<div>
		<!-- Cabecera de Estados y Fecha Inicio y Fin -->
		<div class="vx-row" style="padding-bottom:25px">
			<!-- Cabecera de Estado -->
			<div class="vx-col w-full md:w-1/2 lg:w-1/2 xl:w-1/2">
				<span class="inline-flex relative">
					<div style="padding-right:20px">
						<vx-card class="">
							<p>Estado</p>
						</vx-card>
					</div>
					<vx-card style="height:63px; width:200px">
						<v-select style="margin-top:-6px" @input="filtroEstado(filtros.estado.value)" :options="roleOptions" :clearable="false" v-model="filtros.estado" class="mb-4 md:mb-0" />
					</vx-card>
				</span>
			</div>
			<!-- Cabecera de Fechas -->
			<div class="vx-col w-full md:w-1/2 lg:w-1/2 xl:w-1/2">
				<span class="inline-flex relative">
					<div style="padding-right:20px;">
						<vx-card style="width: 151px;">
							<p>Fecha de Inicio</p>
						</vx-card>
					</div>
					<div style="padding-right:20px;">
						<vx-card style="height:63px">
							<span class="inline-flex relative">
								<IconifyIcon  :icon="calendarIcon" width="20" />
								<flat-pickr @input="filtroFechas(filtros.fechaInicio, filtros.fechaFin)" style="padding-left:15px; margin-top:-6px" :config="configFechaInicio" v-model="filtros.fechaInicio" @on-change="onFromChange" />
							</span>
						</vx-card>
					</div>
					<div style="padding-right:20px;">
						<vx-card style="height:63px">
							<span class="inline-flex relative">
								<IconifyIcon  :icon="calendarIcon" width="20" />
								<flat-pickr @input="filtroFechas(filtros.fechaInicio, filtros.fechaFin)" style="padding-left:15px; margin-top:-6px" :config="configFechaFin" v-model="filtros.fechaFin" @on-change="onToChange" />
							</span>
						</vx-card>
					</div>
				</span>
			</div>
		</div>
		<!-- Tabla de Listado de Campañas -->
		<div class="vx-row">
			<div class="vx-col w-full">
				<tabla-listado :datos="datos"></tabla-listado>				
			</div>
		</div>
	</div>
</template>


<script>
import vSelect from 'vue-select'
import flatPickr from 'vue-flatpickr-component';
import 'flatpickr/dist/flatpickr.css';
import TablaListado from './TablaListado.vue';
import IconifyIcon from '@iconify/vue';
import calendarIcon from '@iconify/icons-bi/calendar-week';
import axios from 'axios';
import datos from './campanias.json';
import datos2 from './campanias.json';
import moment from 'moment';

export default {
	components:{
		vSelect,
		flatPickr,
		TablaListado,
		IconifyIcon,
	},
	data(){
		return{
			datos,
			datos2,
			calendarIcon,
			filtros:{
				fechaInicio: new Date(),
				fechaFin: new Date(),
				estado: { label: 'Todos', value: '0' },
			},
			configFechaInicio: {
				minDate: null,
				maxDate: null,
				dateFormat: 'd-m-Y',
            },
            configFechaFin: {
				minDate: new Date(),
				dateFormat: 'd-m-Y',
            },
			roleOptions: [
				{ label: 'Todos', value: '0' },
				{ label: 'Activas', value: '1' },
				{ label: 'Programadas', value: '2' },
				{ label: 'Finalizadas', value: '3' },
			],
		}
	},
	methods:{
		onFromChange(selectedDates, dateStr, instance) {
			this.$set(this.configFechaFin, 'minDate', dateStr);
		},
		onToChange(selectedDates, dateStr, instance) {
			this.$set(this.configFechaInicio, 'maxDate', dateStr);
		},
		cargarCampanias(){
			axios.get(this.url.campanias).then((res)=>{
				this.datos = res.data;
				this.datos2 = res.data;
			}).catch((error) =>{
				this.$vs.notify({
					color:'danger',
					title:'Error al cargar la Tabla',
					text: error,
					iconPack: 'feather', icon:'icon-alert-circle'
				});
				console.log(error);
			});
		},
		filtroEstado(estados){
			this.datos = this.datos2;
			if(estados === '1'){
				this.datos = this.datos.filter(valor => valor.estado == '1');
			}else if(estados === '2'){
				this.datos = this.datos.filter(valor => valor.estado == '2');
			}else if(estados === '3'){
				this.datos = this.datos.filter(valor => valor.estado == '3');
			}else{
				this.datos = this.datos2;
			}
		},
		filtroFechas(fechaInicio, fechaFinal){
			this.datos = this.datos2;
			let fechaI = moment(fechaInicio, 'DD-MM-YYYY').subtract(1, 'd');
			let fechaF = moment(fechaFinal, 'DD-MM-YYYY').add(1, 'd');
			this.datos = this.datos.filter(valor => moment(valor.fechaInicio, 'DD-MM-YYYY').isBetween(fechaI, fechaF));
		}
	},
	computed:{
		url(){
			return this.$store.state.urlsAPI;
		},
		infoUser(){
			return this.$store.state.loginInfo;
		}
	},
	mounted() {
		this.cargarCampanias();
	},
}
</script>


<style scoped>
.flatpickr-input{
    width: 96px;
	border: 0px;
}
</style>